import React from 'react';
import ReactDOM from 'react-dom';
import {Radar} from 'react-chartjs-2';


const REPORT_DATA = {
    "VFO Detainee-Nonmurder": {"Admissions": 2250, "LOS": 190},
    "VFO Detainee-Murder": {"Admissions": 250, "LOS": 545},
    "Nonviolent Felony Detainee": {"Admissions": 1200, "LOS": 45},
    "Misdemeanor Detainee": {"Admissions": 700, "LOS": 45},
    "Technical Parole Violator": {"Admissions": 1310, "LOS": 30},
    "City Sentenced": {"Admissions": 500, "LOS": 90},
    "Other": {"Admissions": 1600, "LOS": 30}
}

function round5(x) {
    return Math.ceil(x / 5) * 5;
}

function sum(values, valueof = (value) => value) {
    let sum = 0;
    for (const value of values)
        sum += valueof(value)
    return sum;
}

function Row(props) {
    return <tr>
        <td>{props.title}</td>
        <td><input type="range" value={props.Admissions} min="0" max="2500"
                   onChange={e => props.update(props.title, "Admissions", e.target.value)}
                   style={{border: "1px solid black", verticalAlign: "middle", width: "70px"}}
        /></td>
        <td style={{paddingRight: "30px"}}><input type="number" value={props.Admissions} min="0" max="2500"
                                                  onChange={e => props.update(props.title, "Admissions", e.target.value)}
                                                  style={{
                                                      border: "1px solid black",
                                                      textAlign: "center",
                                                      verticalAlign: "middle",
                                                      width: "60px"
                                                  }}
        /> people
        </td>

        <td>
            <input type="range" value={props.LOS} min="0" max="1000"
                   onChange={e => props.update(props.title, "LOS", e.target.value)}
                   style={{border: "1px solid black", verticalAlign: "middle", width: "70px"}}/></td>
        <td><input type="number" value={props.LOS} min="0" max="1000"
                   onChange={e => props.update(props.title, "LOS", e.target.value)}
                   style={{width: "60px", textAlign: "center", border: "1px solid black", verticalAlign: "middle"}}
        /> days
        </td>

        <td style={{
            width: "120px",
            textAlign: "center",
            fontWeight: "bolder",
            margin: "2px",
            verticalAlign: "middle",
            padding: "0 15px"
        }}>{round5(props.Admissions * props.LOS / 365).toLocaleString()} people
        </td>

    </tr>
}


class Calculator extends React.Component {
    constructor(props) {
        super(props);
        this.state = REPORT_DATA;
        this.update = this.update.bind(this);
    }

    update(category, column, value) {
        // We must create a new object and avoid modifying the original REPORT_DATA!
        const newData = {...this.state[category], [column]: +value};
        this.setState({[category]: newData});
    }

    chartData() {
        return {
            labels: Object.keys(this.state),
            datasets: [
                {
                    label: 'ADP',
                    data: Object.values(this.state).map(d => round5(d.Admissions * d.LOS / 365)),
                    backgroundColor: 'rgba(255, 99, 132, 0.2)',
                    borderColor: 'rgba(255, 99, 132, 1)',
                    borderWidth: 1,
                },
            ],
        };
    }

    render() {
        return (<>
            <button onClick={() => this.setState(REPORT_DATA)}>Report recommendations</button>
            <table>
                <thead>
                <tr>
                    <td/>
                    <td colSpan={2} style={{textAlign: "center"}}>Annual Admissions</td>
                    <td colSpan={2} style={{textAlign: "center"}}>Average Length of Stay</td>
                    <td>Daily Population</td>
                </tr>
                </thead>
                <tbody>
                {Object.keys(REPORT_DATA).map(d =>
                    <Row key={d} title={d} Admissions={this.state[d].Admissions} LOS={this.state[d].LOS}
                         update={this.update}/>
                )}
                <tr>
                    <td>Total</td>
                    <td/>
                    <td style={{
                        textAlign: "center",
                        fontWeight: "bold"
                    }}>{sum(Object.values(this.state), d => d.Admissions).toLocaleString()} people
                    </td>
                    <td/>
                    <td/>
                    <td style={{
                        textAlign: "center",
                        fontWeight: "bold"
                    }}>{sum(Object.values(this.state), d => round5(d.Admissions * d.LOS / 365)).toLocaleString()} people
                    </td>
                </tr>
                </tbody>
            </table>
            <br/>
            <h4>NOTES</h4>
            <p>
                Average LOS is calculated only for those discharged.
                <br/>
                City sentenced bed count only reflects those entering with a city sentence. Detainees, specifically
                nonviolent felony and misdemeanor detainees, may enter as pretrial detainees but leave after a city
                sentence. This calculator accounts for their total jail stay, but categorizes them by their initial
                admission status.
            </p>
            <div>
                <Radar data={this.chartData()} width={400} height={400}
                       options={{
                           maintainAspectRatio: false,
                           scale: {ticks: {beginAtZero: true}, max: 2500}
                       }}
                />
            </div>
        </>)
    }
}


ReactDOM.render(
    <div style={{marginLeft: "10px", marginTop: "10px"}}>
        <Calculator/>
    </div>,
    document.getElementById("root"));
